.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%

}

.app-align{
  display: flex;
}


.content-page{
  display: flex;
  width: 70vw;
  flex-direction: column;
  justify-content: space;
  height: 100vh;
  background: #EAE7DC;
  border-right-style: solid;
  border-right-width: 3px
  
}
.content-page-projects{
  display: flex;
  width: 70vw;
  flex-direction: column;
  justify-content: space;
  height: 100vh;
  background: rgb(234, 228, 220);
  border-right-style: solid;
  border-right-width: 1px;
  
}

.content-page-about{
  display: flex;
  width: 70vw;
  flex-direction: column;
  justify-content: space;
  height: 100vh;
  background: rgb(220, 227, 234);
  border-right-style: solid;
  border-right-width: 1px;
  
}

.content-page-experience{
  display: flex;
  width: 70vw;
  flex-direction: column;
  justify-content: space;
  height: 100vh;
  background: rgb(220, 234, 224);
  border-right-style: solid;
  border-right-width: 1px;
  
}
.page-content{
  margin-top: 4vh;
  display: flex;
  width: 100%;
  flex-direction: column;
  height: 100%;
  align-items: center;
  color: rgba(0, 0, 0, 0.61);
  

}

.animated-div{
  height: 100%;
  position: absolute
}




.nav-container{
  display: flex;
  width: 30vw;
  flex-direction: column;
  justify-content: space;
  height: 100vh;
  background: rgb(250, 250, 250);
  color: rgba(0, 0, 0, 0.61);
  align-items: center;
  border-right-style: solid;
  border-right-width: 7px;
  border-color: rgba(0, 0, 0, 0.76);

}

.nav-style{
  display: flex;
  color: rgba(0, 0, 0, 0.651);
  text-decoration: none;
  transition-duration: .3s
}

.nav-style:hover {
  transform: scale(1.4);
  text-decoration: none;
  color: rgba(0, 0, 0, 0.651)
  
}

.position {
  width: 100%
}

.nav-name{
  font-size: 5vmin;
  display: flex;
  flex-direction: column;
  height: 25vh;
  margin-top: 5vh;
}

.socials{
  width: 100%;
  display: flex;
  flex: 1;
  justify-content: space-evenly;
  padding: 10px;
  align-items: flex-end;
  padding-bottom: 5vh

  
}

.name{
  font-size: 5vmin;
  display: flex;
  flex-direction: column;
  
  margin-top: 5vh;

  
}

.nav-links{
  flex: 1;
  display:flex;
  flex-direction: column;
  list-style: none;
  
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  width: 100%;
  overflow: hidden;
  font-size: 3vmin;
  margin: 20px;
  
  
  

  



}

ul{
padding-inline-start: 0px;
}


